import React, { useState } from 'react';
import './Project.css';
import Modal from 'react-modal';

import { disableBodyScroll, enableBodyScroll } from 'body-scroll-lock';

Modal.setAppElement('#root');

export default function Project(props) {
	let targetElement = null;

	const [ showModal, setShowModal ] = useState(false);

	const contentRef = (node) => {
		if (node !== null) {
			targetElement = node;
			disableBodyScroll(targetElement);
		}
	};

	const handleOpenModal = () => {
		setShowModal(true);
	};

	const handleCloseModal = () => {
		setShowModal(false);
		enableBodyScroll(targetElement);
	};

	const generateImgs = (imgs) => {
		return (
			<div
				className={
					props.direction === 'column' ? (
						'app-imgs-container-col'
					) : (
						'app-imgs-container'
					)
				}
			>
				{imgs.map((img) => (
					<div className="app-img-container" key={img.id}>
						<img key={img.id} src={img.img} alt="" />
					</div>
				))}
			</div>
		);
	};

	const { thumbnail, name, description, images } = props;

	return (
		<div className="thumbnail-container">
			<img src={thumbnail} onClick={handleOpenModal} alt="" />
			<Modal
				contentRef={contentRef}
				closeTimeoutMS={300}
				isOpen={showModal}
				contentLabel="modal-content"
				className="Modal"
				overlayClassName="Overlay"
			>
				<div className="close-button" onClick={handleCloseModal}>
					<span>X</span>
				</div>
				<h1>{name}</h1>
				{props.linkText === 'hyper-tempo.com' && (
					<div>
						<a href={props.link} target="_blank">
							{props.linkText}
							<br />
							<br />
						</a>
						<a href="https://youtu.be/i9mFLI4U1WQ" target="_blank">
							Demo (Youtube)
							<br />
							<br />
						</a>
					</div>
				)}
				<p>{description}</p>
				{generateImgs(images)}
			</Modal>
		</div>
	);
}
